.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;

  &-row {
    border-bottom: 1px solid rgba(235, 242, 248, 0.1);

    display: flex;
    align-items: center;

    &_first {
      height: 72px;
    }

    &_second {
      height: 55px;
    }

    &_black {
      border-bottom: 1px solid #EBF2F8;
    }

    &__wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &-burger {
    width: 21px;
    height: 18px;

    .burger__item {
      background-color: #FFFFFF;
      height: 2px;
      border-radius: 30px;
    }

    &_black {
      .burger__item {
        background-color: #000;
      }
    }
  }

  &__block {
    &_info {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }

    &_burger {
      margin-left: 24px;
    }
  }

  &__logo {
    .logo__img {
      width: 200px;
      height: 60px;
    }
  }

  &__phone, &__address {
    &_black {
      color: #000;
    }

    color: #fff;
    font-size: 14px;

  }

  &__phone {
    margin-bottom: 2px;
    font-weight: 500;
  }

  &__btn {
    width: 140px;
    height: 35px;
  }
}

.logo {
  display: flex;
  align-items: center;

  &__text {
    &_black {
      color: #000;
    }

    text-transform: uppercase;
    font-size: 12px;
    color: rgba(#fff, .7);
    line-height: 1.5;
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &__img + &__text {
    margin-left: 50px;
  }
}

.nav {
  &-list {
    display: flex;
    align-items: center;

    &__item + &__item {
      margin-left: 40px;
    }

    &__item + &__item_catalog {
      margin-left: 65px;
    }
  }

  &-item {
    &__link {
      font-weight: 500;
      font-size: 15px;
      color: #fff;
      transition: .3s color;

      &_black {
        color: #000;
      }

      &:hover {
        color: #6A6E7B
      }
    }

    &__catalog {
      display: flex;
      align-items: center;
      padding-right: 32px;
      border-right: 1px solid rgba(221, 232, 241, 0.2);

      &_black {
        border-right: 1px solid #DDE8F1;
      }
    }
  }
}

.mobile-nav {
  width: 100%;
}

.nav-row__close {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 20px;
  top: 20px;

  svg {
    width: 100%;
    height: 100%;
  }

  &:hover {
    cursor: pointer;
  }
}

@media screen and (min-width: 991px) {
  .header {
    &-row {
      &_second {
        .header__block_info {
          display: none;
        }
      }
    }

    &__block {
      &_burger {
        display: none;
      }
    }
  }

  .nav-row__close {
    display: none;
  }
}

@media screen and (max-width: 1170px) and (min-width: 991px) {
  .header {

  }
  .nav {
    &-item{
      &__catalog{
        padding-right: 16px;
      }
      &__link{
        font-size: 14px;
      }
    }
    &-list {
      &__item + &__item{
        margin-left: 20px;
      }
      &__item + &__item_catalog {
        margin-left: 20px;
      }
    }
  }
}

@media screen and (max-width: 990px) {
  .header {
    &__btn {
      margin-top: 33px;
    }

    &__logo {
      .logo__img {
        width: 140px;
        height: 40px;
      }
    }
  }
  .header-row_first {
    .header__block_info {
      margin-left: auto;
    }
  }

  .mobile-nav {
    max-width: 280px;
    padding: 56px 0;
    background-color: #fff;
    height: 100%;
  }
  .header-row_second {
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(39, 42, 47, 0.8);
    width: 100%;
    height: 100vh;
    align-items: flex-start;
    justify-content: flex-start;

    opacity: 0;
    z-index: -1;
    transition: 0.3s all;

    &.show {
      opacity: 1;
      z-index: 99;
    }

    .header-row__wrapper {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
    }

    .header__block_info {
      margin-top: 40px;
      align-items: flex-start;
    }

    .header__address, .header__phone {
      color: #000;
    }
  }
  .nav {
    &-list {
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;

      &__item + &__item {
        margin-left: 0;
        margin-top: 11px;
      }
    }

    &-item {
      &__link {
        color: #000;
      }
    }
  }
  .catalog-burger {
    display: none;
  }
}