.section{
  &-objects{
    margin-top: 76px;
  }
  &-catalog{
    &__wrapper{
      padding: 40px 0 80px 0;
    }
    &__block + &__block{
      margin-top: 30px;
    }
  }
  &-services{
    margin-top: 50px;
  }
}

.wrapper-404{
  &__text-error{
    font-size: 16px;
    font-weight: 600;
    color:#FF4D35;
    text-align: center;
  }
  &__title{
    margin-top: 15px;
    font-size: 26px;
    font-weight: 600;
    text-align: center;
  }
  &__text{
    font-size: 15px;
    font-weight: 500;
    text-align: center;
    margin-top: 10px;
  }
  &__img{
    height: 100px;
    width: 100%;
    margin-top: 55px;
    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}



@media screen and (min-width: 991px) {
  .wrapper-404{
    &__text-error{
      font-size: 18px;
    }
    &__title{
      font-size: 32px;
    }
    &__text{
      font-size: 18px;
      max-width: 535px;
      margin: 15px auto auto auto;
      line-height: 1.52;
    }
    &__img{
      height: 130px;
    }
  }
  .error-page-wrapper{
    padding-bottom: 235px;
  }
  .section{
    &-catalog{
      &__wrapper{
        padding: 80px 0 130px 0;
      }
      &__block + &__block{
        margin-top: 70px;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .section{
    &-services{
      .container{
        padding-right: 0;
      }
    }
  }
}

@media screen and (max-width: 500px){
  .section{
    &-services{
      display: none;
    }
  }
}