.catalog {
  &-preview {
    &__grid {
      display: grid;
      grid-template-columns:minmax(0, 1fr);
      gap: 30px;
      margin-top: 20px;
    }

    &__item {
      display: flex;
      justify-content: center;
      flex-direction: column;
    }

    &__title {
      font-weight: 600;
      font-size: 30px;
      line-height: 1.18;
    }

    &__desc {
      margin-top: 20px;
      font-size: 16px;
      line-height: 1.45;
    }

    &__img {
      height: 314px;
      width: 100%;
      border-radius: 13px;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &-box{
    padding-bottom: 80px;
    border-bottom: 1px solid #E3E3E3;
    &__search{
      &-result{
        margin-top: 40px;
      }
      &-text{
        font-weight: 500;
        font-size: 16px;
      }
      &-wrapper{
        margin-top: 30px;
      }
      &-pagination{
        margin-top: 36px;
      }
    }
  }
  &__item{
    display: grid;
    grid-template-columns: 335px auto;
    gap: 42px;

    padding: 20px 10px;
    border-top: 1px solid #F0F0F0;
    transition: 0.3s all;
    box-shadow: 0px 0px 20px transparent;
    border-radius: 5px;

    &:hover{
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.07);
    }
    &:last-child{
      border-bottom: 1px solid #F0F0F0;
    }
  }
}


@media screen and (min-width: 991px) {
  .catalog {
    &-preview {
      &__grid {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 160px;
        margin-top: 10px;
      }

      &__title {
        font-size: 38px;
      }

      &__desc {
        font-size: 17px;
        margin-top: 30px;
      }
    }
  }
}

@media screen and (max-width: 990px) {
  .catalog {
    &-preview {
      &__item {
        &:nth-child(2) {
          grid-row-start: 1;
          grid-row-end: 2;
        }

        &:nth-child(1) {
          grid-row-start: 2;
          grid-row-end: 3;
        }
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .breadcrumb{
    display: none;
  }
  .catalog{
    &-preview{
      &__grid{
        gap: 14px;
      }
      &__img{
        height: 170px;
      }
      &__title{
        font-size: 24px;
        max-width: 215px;
      }
      &__desc{
        font-size: 14px;
        margin-top: 10px;
      }
    }
  }
}