.product{
  &__img{
    width: 100%;
    height: 226px;
    border-radius: 5px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title{
    font-size: 22px;
    font-weight: 500;
    color:#3068F6;
  }
  &__info{
    font-size: 16px;
    font-weight: 500;
    margin-top: 5px;
    color:#151515;
  }
  &__address{
    font-size: 16px;
    color:#151515;
    line-height: 1.12;
  }
  &__price{
    margin-top: 30px;
    font-weight: 600;
    font-size: 22px;
  }
  &__text{
    color:#6B7176;
    font-size: 15px;
    line-height: 1.37;
  }
  &-pagination{
    &__content{
      display: flex;
      align-items: center;
    }
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__arrow{
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  &-content{
    display: flex;
    flex-direction: column;
    &__desc{
      margin-top: 25px;
    }
    &__footer{
      margin-top: auto;
    }
  }
}

.pagination{
  &-item + &-item{
    margin-left: 2px;
  }
  &-item{
    border-radius: 4px;
    &.active{
      background: #F0F6FA;
    }
    &__link{
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;

      font-weight: 500;
      font-size: 15px;
      color:#151515;
    }
  }
}

@media screen and (max-width: 991px) {
  .catalog{
    &__item{
      grid-template-columns: 250px auto;
    }
    &-box{
      &__search{
        &-result{
          margin-top: 60px;
        }
        &-pagination{
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }
  .product{
    &__img{
      height: 170px;
    }
    &__text{
      font-size: 13px;
    }
    &__price{
      font-size: 16px;
      margin-top: 15px;
    }
    &__address{
      font-size: 13px;
    }
    &__info{
      font-size: 14px;
    }
    &__title{
      font-size: 16px;
    }
    &-content{
      &__desc{
        margin-top: 13px;
      }
      &__footer{
        margin-top: 16px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .catalog{
    &__item{
      grid-template-columns: minmax(0,1fr);
      gap: 15px;
    }
    &-box{
      &__search{
        &-wrapper{
          margin-top: 15px;
        }
      }
    }
  }
  .product{
    &__img{
      height: 226px;
    }
  }
}