.services {
  &-block {
    &__title{
      font-size: 26px;
      font-weight: 600;
    }
    &__grid {
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      grid-auto-rows: 70px;
      gap: 0;
      margin-top: 30px;
      min-width: 945px;
    }
  }
  &-item + &-item{
    border-left: 1px solid #E1E1E1;
    padding-left: 25px;
  }
  &-item {
    display: flex;
    align-items: flex-start;
    &__icon {
      width: 43px;
      height: 43px;
      border-radius: 10px;

      display: flex;
      align-items: center;
      justify-content: center;

      flex-shrink: 0;

      background: #FF4D35;

      svg {
        max-height: 25px;
      }
    }

    &__icon + &__content {
      margin-left: 20px;
    }

    &__content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      width: 100%;
      height: 100%;
    }

    &__title {
      font-weight: 500;
      font-size: 16px;
      line-height: 1.18;
    }

    &__text {
      font-size: 13px;
      line-height: 1.25;
      color: rgba(#151515, .8)
    }
  }
}

@media screen and (min-width: 991px) {
  .services{
    &-block{
      &__grid{
        gap: 50px;
        grid-auto-rows: 76px;
      }
    }
    &-item + &-item{
      padding-left: 50px;
    }
    &-item{
      &__icon + &__content{
        margin-left: 25px;
      }
      &__title{
        font-size: 18px;
      }
      &__text{
        font-size: 14px;
      }
    }
  }
}