*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Graphik';
  src: url("../fonts/Graphik-Medium/Graphik-Medium.woff2") format("woff2"), url("../fonts/Graphik-Medium/Graphik-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Graphik';
  src: url("../fonts/Graphik-Regular/Graphik-Regular.woff2") format("woff2"), url("../fonts/Graphik-Regular/Graphik-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Graphik';
  src: url("../fonts/Graphik-Bold/Graphik-Bold.woff2") format("woff2"), url("../fonts/Graphik-Bold/Graphik-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

body {
  font-family: 'Graphik', sans-serif;
  font-weight: 400;
  color: #000;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

.page-wrapper {
  flex: 1 0 auto;
}

.page-item {
  padding-top: 127px;
}

.page-item__content {
  padding-top: 30px;
  background: #F4F4F4;
}

.container {
  max-width: 1320px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 600px) and (max-width: 991px) {
  .container {
    padding: 0 60px;
  }
}

.catalog-block__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.catalog-block__label {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 32px;
  color: #151515;
}

.catalog-block__label svg {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.catalog-block__link {
  font-size: 18px;
  font-weight: 500;
  color: #FF4D35;
}

.catalog-block__container {
  margin-top: 25px;
}

.btn {
  -webkit-appearance: none;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn:hover {
  cursor: pointer;
}

.btn-orange {
  background-color: #FF4D35;
  border-radius: 4px;
  font-size: 13px;
  color: #Fff;
  font-weight: 500;
  transition: .3s background-color;
}

.btn-orange:hover {
  background-color: #F24028;
}

.burger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}

.burger__item {
  width: 100%;
}

.burger:hover {
  cursor: pointer;
}

.catalog-burger {
  width: 14px;
  height: 12px;
  margin-right: 26px;
}

.catalog-burger .burger__item {
  height: 2px;
  background-color: #fff;
  border-radius: 30px;
}

.catalog-burger_black .burger__item {
  background-color: #000;
}

.page-wrapper {
  padding-top: 150px;
}

.breadcrumb__list {
  display: flex;
  align-items: center;
}

.breadcrumb-list__item + .breadcrumb-list__item::before {
  content: '/';
  margin: 0 10px;
  color: #828589;
}

.breadcrumb-list__item:last-child {
  pointer-events: none;
}

.breadcrumb-item__link {
  font-size: 13px;
  color: #828589;
  transition: .3s color;
}

.breadcrumb-item__link:hover {
  color: #47484A;
}

.custom-select-panel {
  background-color: #fff;
}

.custom-select-container {
  height: 100%;
}

.custom-select-opener {
  display: flex;
  align-items: center;
  height: 100%;
  background-color: transparent;
  position: relative;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding-right: 15px;
  color: #858C91;
  transition: .3s color;
}

.custom-select-opener span {
  text-overflow: ellipsis;
  overflow: hidden;
}

.custom-select-opener::before {
  content: '';
  width: 13px;
  height: 13px;
  display: block;
  background: url("../img/arrow-select.svg") center center no-repeat;
  background-size: contain;
  position: absolute;
  top: 50%;
  margin-top: -6.5px;
  right: 0;
  transition: 0.3s all;
}

.custom-select-opener:hover {
  color: #FF4D35;
}

.custom-select-option {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  transition: .3s color;
}

.custom-select-option:hover {
  cursor: pointer;
  background-color: transparent;
  color: #FF4D35;
}

.custom-select-option.is-selected::before {
  display: none;
}

.custom-select-container.is-open .custom-select-panel {
  overflow-y: hidden;
}

.custom-select-container.is-open .custom-select-opener::before {
  transform: rotate(180deg);
}

@media screen and (max-width: 991px) {
  .catalog-block__label {
    font-size: 24px;
  }
  .catalog-block__link {
    font-size: 14px;
  }
  .catalog-block__container {
    padding-right: 0;
  }
}

.item-img-slider .swiper-pagination-bullet {
  width: 6px;
  height: 6px;
  background-color: #ffffff;
  opacity: .4;
}

.item-img-slider .swiper-pagination-bullet-active {
