.object-item {
  padding: 14px 12px;
  position: relative;
  border-radius: 13px;
  overflow: hidden;
}

.object-item::before {
  content: '';
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  background: rgba(10, 10, 10, 0.34);
  opacity: 0;
  transition: 0.3s opacity;
}

.object-item:hover::before {
  opacity: 1;
}

.object-item__img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1;
}

.object-item__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.object-item__content {
  position: relative;
  z-index: 3;
}

.object-item__title, .object-item__text {
  color: #fff;
}

.object-item__title_black, .object-item__text_black {
