}

.catalog-search__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.catalog-search__wrapper .search-block {
  border: 1px solid #E2E2E2;
}

.catalog-search__submit {
  width: 200px;
  height: 66px;
  margin-left: 14px;
  border-radius: 6px;
}

@media screen and (min-width: 991px) and (max-width: 1220px) {
  .catalog-search__wrapper {
