  .catalog-search__wrapper .search-block {
    border: none;
  }
  .catalog-search__submit {
    height: 48px;
    margin-left: 0;
    margin-top: 30px;
    width: 100%;
  }
}

@media screen and (max-width: 450px) {
  .catalog-search {
    margin-top: 30px;
  }
  .catalog-search__submit {
