}

.product__address {
  font-size: 16px;
  color: #151515;
  line-height: 1.12;
}

.product__price {
  margin-top: 30px;
  font-weight: 600;
  font-size: 22px;
}

.product__text {
  color: #6B7176;
  font-size: 15px;
  line-height: 1.37;
}

.product-pagination__content {
  display: flex;
  align-items: center;
}

.product-pagination__wrapper {
  display: flex;
  align-items: center;
}

.product-pagination__arrow {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.product-content {
  display: flex;
  flex-direction: column;
}

.product-content__desc {
