  }
  .product__img {
    height: 226px;
  }
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 90px;
  background: rgba(31, 32, 34, 0.67);
  opacity: 0;
  z-index: -1;
  transition: .3s all;
}

.popup.show {
  opacity: 1;
  z-index: 999;
}

.popup__wrapper {
  max-width: 380px;
  width: 100%;
  position: relative;
}

.popup__close {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 0;
  top: -40px;
}

.popup__close:hover {
  cursor: pointer;
}

.popup__close svg {
  width: 100%;
  height: 100%;
}

.popup-form__wrapper {
  background-color: #fff;
  padding: 50px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06);
  border-radius: 6px;
}

.popup-form__input {
  position: relative;
}

.popup-form__input .text-input {
  height: 42px;
  font-size: 15px;
}

.popup-form__input + .popup-form__input {
  margin-top: 20px;
}

.popup-form__footer {
  margin-top: 15px;
}

.popup-form__submit {
  height: 42px;
  width: 100%;
}

.popup-form__title {
  font-size: 21px;
  font-weight: 600;
  text-align: center;
}

.popup-form__content {
  margin-top: 20px;
}

.thanks-wrapper {
  min-height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  padding: 0 70px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06);
  border-radius: 6px;
}

.thanks-wrapper__icon {
  width: 47px;
  height: 47px;
  background: #DAFFD3;
  border-radius: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.thanks-wrapper__icon svg {
  width: 25px;
  height: 18px;
}

.thanks-wrapper__title, .thanks-wrapper__text {
  text-align: center;
}

.thanks-wrapper__title {
  font-size: 21px;
  font-weight: 600;
  margin-bottom: 10px;
}

.thanks-wrapper__text {
  color: #151515;
  font-size: 15px;
  line-height: 20px;
}

@media screen and (min-width: 991px) {
  .popup__close {
    display: none;
  }
}

@media screen and (min-width: 450px) {
  .popup__close {
    top: 0;
    right: -40px;
  }
}
