.section {
  &-form {
    &__title {
      font-weight: 600;
      font-size: 28px;
    }

    &__desc {
      line-height: 1.33;
      font-size: 12px;
      margin-top: 10px;
      max-width: 405px;
    }
  }
}

.request {
  &-form {
    &__title {
      font-size: 22px;
      font-weight: 600;
      color: #1A1A1A;
    }

    &__desc {
      font-size: 14px;
      line-height: 22px;
      color: #0E0E0E;

      margin-top: 3px;
    }

    &__content {
      margin-top: 17px;
    }

    &__input + &__input {
      margin-top: 20px;
    }

    &__input{
      position: relative;
    }

    &__footer {
      margin-top: 24px;
    }

    &__submit {
      width: 100%;
      height: 58px;
      font-size: 18px;
      border-radius: 10px;
    }
  }
}

.form{
  &-privacy{
    display: flex;
    align-items: flex-start;
    margin-top: 16px;
    p{
      font-size: 12px;
      color: #858C91;
      a{
        color:#1C91FD;
      }
    }
  }
  &-checkbox{
    label{
      display: flex;
      align-items: flex-start;
      &:hover{
        cursor: pointer;
      }
      input{
        margin-right: 10px;
      }
    }
  }

}

@media screen and (min-width: 991px) {
  .section {
    &-form {
      &_catalog{
        margin-top: 113px;
      }
      &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      &__item {
        position: relative;
        z-index: 2;
      }

      &__wrapper {
        position: relative;
        padding: 70px 0 185px 0;
      }

      &__title {
        font-size: 48px;
      }

      &__desc {
        margin-top: 35px;
        font-size: 19px;
      }

      &__grid {
        display: flex;
        justify-content: space-between;
      }

      &__info {
        max-width: 450px;
        width: 100%;
      }

      &__submit{
        height: 64px;
      }
    }
  }
  .request {
    &-form {
      &__wrapper {
        max-width: 470px;
        width: 100%;
        background: #FFFFFF;
        border-radius: 14px;
        padding: 50px 42px;
      }

      &__title {
        font-size: 30px;
      }

      &__desc {
        font-size: 18px;
        line-height: 25px;

        margin-top: 14px;
      }

      &__footer {
        margin-top: 30px;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .section{
    &-form{
      &_catalog{
        margin-top: 100px;
      }
      &__wrapper{
        .container{
          padding:0;
        }
      }
      &__item{
        padding:0 60px;
      }
      &__img{
        margin: 30px 0 40px 0;
        height: 416px;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .section{
    &-form{
      &_catalog{
        margin-top: 55px;
      }
      &__item{
        padding:0 15px;
      }
      &__img{
        height: 240px;
      }
    }
  }
}

