.section{
  &-hero{

    min-height: 365px;

    display: flex;
    align-items: center;
    &__wrapper{
      width: 100%;
      max-width: 1045px;
      margin: 0 auto;
    }
    &__search{
      margin-top: 30px;
    }
  }
}

.hero{
  &-title{
    &__text{
      font-weight: 700;
      font-size: 40px;
      color:#fff;
      line-height: 1.1;
      text-shadow: 0px 1.11019px 0px rgba(0, 0, 0, 0.39);

      max-width: 665px;
      width: 100%;
    }
  }
  &-search{
    width: 100%;
    &__submit{
      width: 215px;
      height: 45px;
      margin: 15px 0 0 auto;
      border-radius: 6px;
    }
  }
}

@media screen and (min-width: 991px) {
  .section{
    &-hero{
      min-height: 625px;

      background: url("../img/hero.jpg") center center no-repeat;
      background-size: cover;
      &__search{
        .search-block{
          box-shadow: 0px 4px 60px rgba(0, 0, 0, 0.65);
        }
      }
      &__wrapper{
        padding-top: 130px;
        .container{
          padding: 0;
        }
      }
    }
  }
  .room{
    &-input{
      &:first-child{
        .radio-input__checkmark{
          padding-left: 0;
          border-left: none;
        }
      }
      &:last-child{
        .radio-input__checkmark{
          padding-right: 0;
        }
      }
    }
  }
}


@media screen and (max-width: 990px) {

  .hero{
    &-title{
      min-height: 293px;
      background: url("../img/hero.jpg") center center no-repeat;
      background-size: cover;
      display: flex;
      align-items: flex-end;
      padding: 35px 0;
      &__text{
        font-size: 32px;
      }
    }
    &-search{
      &__submit{
        width: 100%;
        margin-top: 30px;
      }
    }
  }

}