.footer{
  &__row{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    &_second{
      padding:35px 0 27px 0;
    }
  }
  &__wrapper{
    padding:45px 0 0 0;
  }
  &__copyright{
    color: rgba(10, 37, 51, 0.6);
    font-size: 16px;
    line-height: 1.5;
  }
  &__logo{
    width: 400px;
    height: 110px;
  }
  &-privacy{
    &__link{
      color: rgba(10, 37, 51, 0.6);
      font-size: 16px;
      line-height: 1.5;
    }
  }
  &__block{
    &_list{
      display: flex;
      margin-top: 40px;
    }
  }
  &-info{
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    &__item{
      display: flex;
      align-items: flex-start;
    }
    &__item + &__item{
      margin-top: 7px;
    }
    &__img{
      width: 23px;
      height: 23px;
      margin-right: 12px;
      flex-shrink: 0;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &__text{
      color:#151515;
      font-size: 16px;
      line-height: 1.53;
    }
    &__logo{
      max-width: 390px;
      width: 100%;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }
  &-list + &-list{
    margin-left: 104px;
  }
  &-list{
    &__title + &__item{
      margin-top: 40px;
    }
    &__item + &__item{
      margin-top: 13px;
    }
    &__text{
      color:#151515;
      font-weight: 500;
      font-size: 28px;
    }
    &__link{
      color:#151515;
      font-size: 16px;
      line-height: 1.53;
    }
  }
  &-partners{
    display: flex;
    align-items: center;
  }
}


@media screen and (min-width: 991px) {
  .footer{
    &__row{
      flex-direction: row;
      &_second{
        align-items: flex-end;
      }
    }
    &__block{
      &_list{
        margin-top: 0;
      }
    }
    &-info{
      max-width: 340px;
    }
  }
}

@media screen and (max-width: 991px){
  .footer{
    border-top: 1px solid #EFEFEF;
    margin-top: 90px;
    &__row{
      &_second{
        padding-top: 70px;
      }
    }
    &__logo{
      width: 256px;
      height: 75px;
    }
    &-partners{
      order: 1;
      justify-content: space-between;
    }
    &__copyright{
      order: 2;
      margin: 65px 0 12px 0;
    }
    &-privacy{
      order: 3;
    }
  }
}

@media screen and (max-width: 450px) {
  .footer{
    margin-top: 65px;
    &__row{
      &_second{
        padding-top: 50px;
      }
    }
    &__copyright{
      margin-top: 40px;
    }
    &__block{
      &_list{
        flex-direction: column;
      }
    }
    &-list + &-list{
      margin-left: 0;
      margin-top: 33px;
    }
  }
}

@media screen and (min-width: 450px) and (max-width: 991px) {
  .footer{
    &__block{
      &_list{
        justify-content: space-between;
      }
    }
  }
}