.search {
  &-block {
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: space-between;

    background: #FFFFFF;
    border-radius: 7px;

    &__item {
      height: 66px;
      padding: 0 15px;
      display: flex;
      align-items: center;

      &_params {
        flex-shrink: 0;
      }
    }

    &__item + &__item {
      border-left: 1px solid #E9E9E9;
    }
  }

  &-room {
    display: flex;
    align-items: center;
  }

  &-input {
    display: flex;
    align-items: center;

    &__wrapper {
      display: flex;
      align-items: center;
      margin-left: 13px;

      & > span {
        margin-left: 4px;
        font-size: 16px;
        display: block;
      }

      input {
        -webkit-appearance: none;
        border: none;
        border-bottom: 1px solid #CECECE;
        width: 57px;
        height: 15px;
        outline: none;
        line-height: 14px;
      }
    }

    &__item + &__item {
      margin-left: 10px;
    }

    &__item {
      span {
        color: #858C91;
        font-size: 16px;
        margin-right: 5px;
      }
    }
  }

  &-select {
    max-width: 260px;
    width: 100%;
    padding-right: 15px;
    select {
      width: 100%;
      max-width: 260px;
    }
  }

  &-form {
    &__category {
      display: flex;
      align-items: center;
      margin-bottom: 20px;
    }
  }

  &-category {
    &__item + &__item {
      margin-left: 8px;
    }
  }
}

.category {
  &-input {
    width: max-content;
    &__item:checked + &__checkmark {
      background-color: #2C3144;
      color: #fff;
      border-color:transparent;
    }

    &__checkmark {
      display: block;
      padding: 7px 18px;
      border:1px solid #F0F1F1;
      border-radius: 36.9182px;
      font-size: 15px;
      font-weight: 500;
      transition: .3s background-color, .3s color;
      &:hover {
        cursor: pointer;
        background-color: rgba(53, 61, 84, 0.66);
      }
    }
  }
}

.room {
  &-input {

    &__item:checked + &__checkmark {
      color: #3D7DDA;
    }

    &__checkmark {
      padding: 0 20px;
      border-left: 1px solid #DDDEE0;
      font-size: 16px;
      transition: .3s color, .3s background-color;
      &:hover {
        cursor: pointer;
        color: #FF4D35;
      }
    }
  }
}


@media screen and (min-width: 991px) {
  .category {
    &-input {

      &__item:checked + &__checkmark {
        background-color: #fff;
        color: #151515;
      }

      &__checkmark {
        background: rgba(11, 14, 22, 0.66);
        border:none;
        color: #Fff;
      }
    }
  }
  .search{
    &-input{
      &__item{
        &-sign{
          display: none;
        }
      }
    }
    &-block{
      &__item{
        &_search{
          max-width: 260px;
          width: 100%;
        }
      }
    }
  }
}

@media screen and (min-width: 991px) and (max-width: 1240px) {
  .search{
    &-select{
      max-width: unset;
      width: 100%;
    }
    &-block{
      &__item{
        &_search{
          max-width: 180px;
        }
      }
    }
  }
}

@media screen and (max-width: 990px) {
  .logo{
    &__text{
      display: none;
    }
  }
  .search {
    &-block {
      flex-direction: column;
      align-items: flex-start;
      &__item{
        height: auto;
        &_room{
          width: 100%;
        }
        &_search{
          width: 100%;

        }
      }
      &__item + &__item{
        border-left: none;
        margin-top: 10px;
      }
    }
    &-room{
      width: 100%;
      display: grid;
      grid-template-columns: repeat(4,minmax(0,1fr));
      border:1px solid #E4E4E4;
      border-radius: 8px;
    }
    &-select{
      max-width: 100%;
      border-radius: 8px;
      border: 1px solid #E4E4E4;
      height: 48px;
      padding: 0 15px;
    }
    &-input{
      &__title{
        width: 85px;
      }
      &__wrapper{
        height: 48px;
        border: 1px solid #E4E4E4;
        border-radius: 8px;
        &>span{
          display: none;
        }
        input{
          border: none;
        }
        span{
          margin: 0;
        }
      }
      &__item{
        padding: 0 12px;
        height: 100%;
        display: flex;
        align-items: center;
        &-sign{
          width: 11px;
          display: block;
        }
      }
      &__item + &__item{
        margin-left: 0;
        border-left: 1px solid #E4E4E4;
      }
    }
  }
  .room{
    &-input{
      display: flex;
      align-items: center;
      justify-content: center;
      height: 48px;
      border-left: 1px solid #E4E4E4;
      &:first-child{
        border-left: none;
        .room-input__checkmark{
          border-bottom-left-radius: 8px;
          border-top-left-radius: 8px;
        }
      }
      &:last-child{
        .room-input__checkmark{
          border-bottom-right-radius: 8px;
          border-top-right-radius: 8px;
        }
      }
      &__item:checked + &__checkmark {
        background-color: #EFF4FA;
        border:1px solid #3D7DDA;
      }
      &__checkmark{
        border-left: none;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &__label{
        width: 100%;
        height: 100%;

      }
    }
  }
  .header{
    &-row{
      &_first{
        height: 54px;
        .header__address{
          display: none;
        }
        .header__phone{
          margin-bottom: 0;
        }
      }
    }
    &__logo{
      height: 40px;
    }
  }
}

.catalog{
  &-search{
    margin-top: 30px;
    &__wrapper{
      display: flex;
      align-items: center;
      justify-content: space-between;
      .search-block{
        border: 1px solid #E2E2E2;
      }
    }
    &__submit{
      width: 200px;
      height: 66px;
      margin-left: 14px;
      border-radius: 6px;
    }
  }
}

@media screen and (min-width: 991px) and (max-width: 1220px) {
  .catalog{
    &-search{
      &__wrapper{
        flex-direction: column;
      }
      &__submit{
        margin-left: 0;
        margin-top: 10px;
        height: 48px;
      }
    }
  }
}


@media screen and (max-width: 990px) {
  .catalog{
    &-search{
      margin-top: 60px;
      &__wrapper{
        flex-direction: column;
        .search-block{
          border:none;
        }
      }
      &__submit{
        height: 48px;
        margin-left: 0;
        margin-top: 30px;
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .catalog{
    &-search{
      margin-top: 30px;
      &__submit{
        margin-top: 13px;
      }
    }
  }
  .search{
    &-block{
      &__item{
        padding: 0;
        width: 100%;
      }
    }
    &-input{
      width: 100%;
      justify-content: space-between;
    }
  }
  .category{
    &-item{
    }
  }
}