.contact{
  &-wrapper{
    padding-top: 40px;
    padding-bottom: 170px;
    max-width: 1000px;
    width: 100%;
    &__title{
      font-size: 32px;
      font-weight: 600;
      line-height: 1.18;
      margin-bottom: 35px;
    }
  }
  &-block + &-block{
    margin-top: 60px;
  }
  &-block{
    &__title{
      font-weight: 600;
      font-size: 20px;
      margin-bottom: 7px;
    }
    &__text{
      font-size: 15px;
      line-height: 1.52;
    }
  }
}

@media screen and (max-width: 991px) {
  .contact{
    &-wrapper{
      padding: 0;
    }
  }
}

@media screen and (max-width: 450px) {
  .contact{
    &-wrapper{
      &__title{
        font-size: 24px;
      }
    }
    &-block{
      &__title{
        font-size: 18px;
      }
    }
  }
}