.item {
  &-wrapper {
    margin-top: 26px;
    padding-bottom: 84px;
  }

  &-block + &-block {
    margin-top: 12px;
  }

  &-block {
    background-color: #fff;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06);
    border-radius: 6px;
    padding: 35px 25px;

    &_img {
      padding: 15px;
    }

    &_related {
      padding-bottom: 77px;
    }

    &__slider {
      margin-top: 40px;
    }

    &__title {
      font-size: 22px;
      font-weight: 500;
    }

    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 15px;
    }

    &__price {
      font-size: 22px;
      font-weight: 600;
    }

    &__info {
      font-size: 16px;
      color: #151515;
    }

    &__address {
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #151515;
      margin-top: 20px;

      &::before {
        content: '';
        width: 15px;
        height: 15px;
        display: block;
        background: url("../img/pin-second.svg") center center no-repeat;
        background-size: contain;
        margin-right: 10px;
        flex-shrink: 0;
      }
    }

    &__features{
      margin-top: 30px;
    }
  }

  &-slider-img {
    width: 100%;
    height: 340px;

    .swiper-slide {
      width: 90%;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 9px;
    }

    .swiper {
      &-pagination {
        bottom: 25px;

        &-bullet {
          background: rgba(0, 0, 0, 0.37);
          opacity: 1;
          width: 8px;
          height: 8px;

          &-active {
            background-color: #fff;
          }
        }
      }

      &-button {
        &-prev, &-next {
          width: 48px;
          height: 48px;
          background: rgba(0, 0, 0, 0.6);
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;

          &::after {
            color: transparent;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: contain;
            width: 16px;
            height: 16px;
          }
        }

        &-next {
          right: 0;

          &::after {
            background-image: url("../img/left-chevron-white.svg");
          }
        }

        &-prev {
          left: 0;

          &::after {
            background-image: url("../img/right-chevron-white.svg");
          }
        }
      }
    }
  }

  &-wrapper {
    &__grid {
      display: grid;
      grid-template-columns: minmax(0, 1fr);
      gap: 14px;
    }

    &__grid + &__grid {
      margin-top: 10px;
    }
  }

  &-form {
    &__wrapper {
      background-color: #fff;
      padding: 25px;
      box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06);
    }

    &__input {
      position: relative;
      .text-input {
        height: 42px;
        font-size: 15px;
      }
    }

    &__input + &__input {
      margin-top: 20px;
    }

    &__footer {
      margin-top: 15px;
    }

    &__submit {
      height: 42px;
      width: 100%;
    }

    &__title {
      font-size: 18px;
      font-weight: 600;
    }

    &__content {
      margin-top: 20px;
    }
  }

  &__desc {
    margin-top: 23px;
    font-size: 15px;
    color: #151515;
    line-height: 1.37;

    p + p {
      margin-top: 25px;
    }
  }
}

.feature{
  &-item + &-item{
    margin-top: 13px;
  }
  &-block + &-block{
    margin-top: 20px;
  }
  &-block{
    &__content{
      margin-top: 25px;
    }
    &__title{
      font-weight: 500;
      font-size: 15px;
      color:#151515;
    }
  }
  &-item{
    display: flex;
    align-items: center;
    &__title{
      max-width: 230px;
      width: 100%;
      font-size: 15px;
      color:#858C91;
    }
    &__text{
      font-size: 15px;
      color:#151515;
    }
    &__title, &__text{
      line-height: 20px;
    }
  }
}

@media screen and (min-width: 991px) {
  .item{
    &-form{
      &__wrapper{
        border-radius: 6px;
        padding: 40px 26px;
      }
      &__title{
        font-size: 18px;
        text-align: center;
      }
    }
    &-wrapper{
      &__grid{
        grid-template-columns: calc(100% - 315px) 315px;
      }
    }
    &-slider-img{
      height: 480px;
    }
  }
}

@media screen and (min-width: 450px) and (max-width: 990px) {
  .item{
    &-wrapper{
      &__aside{
        display: none;
      }
    }
  }
}

@media screen and (max-width: 449px) {
  .page-item {
    .container {
      padding: 0;
    }
  }
  .item{
    &-wrapper{
      &__grid{
        gap: 5px;
      }
      &__grid + &__grid{
        margin-top: 5px;
      }
    }
    &-slider-img{
      height: 200px;
      .swiper{
        &-button{
          &-next,&-prev{
            width: 32px;
            height: 32px;
            &::after{
              width: 11px;
              height: 11px;
            }
          }
        }
        &-pagination{
          bottom: 10px;
          &-bullet{
            width: 5px;
            height: 5px;
          }
        }
      }
    }
    &-block + &-block{
      margin-top: 5px;
    }
    &-block{
      &_related{
        padding-bottom: 45px;
      }
      &__slider{
        margin-top: 20px;
      }
      &__header{
        flex-direction: column;
        align-items: flex-start;
      }
      &__price{
        margin-top: 7px;
        font-size: 20px;
      }
      &__title{
        font-size: 20px;
      }
      &__info{
        font-size: 14px;
      }
      &__address{
        font-size: 14px;
        align-items: flex-start;
      }
      &__features{
        margin-top: 17px;
      }
    }
    &__price{
      margin-top: 7px;
    }
    &__desc{
      margin-top: 17px;
      font-size: 14px;
    }
  }
  .feature{
    &-block{
      &__content{
        margin-top: 20px;
      }
    }
    &-item{
      &__title{
        max-width: 180px;
      }
      &__title,&__text{
        font-size: 13px;
      }
    }
  }
}