.object{
  &-block{
    &__title{
      font-weight: 600;
      font-size: 26px;
      color:#151515;
      text-align: center;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      grid-auto-rows: 170px;
      gap: 12px;

      margin-top: 25px;
    }
  }
  &-item{
    padding: 14px 12px;
    position: relative;
    border-radius: 13px;
    overflow: hidden;
    &::before{
      content: '';
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 2;
      background: rgba(10, 10, 10, 0.34);
      opacity: 0;
      transition: 0.3s opacity;
    }
    &:hover{
      &::before{
        opacity: 1;
      }
    }
    &__img{
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 1;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__content{
      position: relative;
      z-index: 3;
    }
    &__title,&__text{
      color:#fff;
      &_black{
        color:#000;
      }
    }
    &__title{
      font-size: 16px;
      font-weight: 500;
      line-height: 1.21;
    }
    &__text{
      font-size: 11px;
      line-height: 1.3;
      margin-top: 5px;
    }
  }
}
@media screen and (min-width: 500px) {
  .object{
    &-block{
      &__grid{
        grid-template-columns: repeat(4,minmax(0,1fr));
      }
    }
  }
}


@media screen and (min-width: 1024px){

  .object{
    &-block{
      &__title{
        font-size: 32px;
        text-align: left;
      }
      &__grid{
        grid-auto-rows: 320px;
        gap: 25px;
      }
    }
    &-item{
      padding: 25px;

      &__title{
        font-size: 24px;
      }
      &__text{
        font-size: 16px;
      }
    }
  }
  .related-block{
    margin-top: 55px;
    &__title{
      text-align: center;
    }
  }
}