.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 90px;

  background: rgba(31, 32, 34, 0.67);
  opacity: 0;
  z-index: -1;

  transition: .3s all;
  &.show{
    opacity: 1;
    z-index: 999;
  }
  &__wrapper{
    max-width: 380px;
    width: 100%;
    position: relative;
  }

  &__close{
    width: 20px;
    height: 20px;
    position: absolute;
    right: 0;
    top: -40px;
    &:hover{
      cursor: pointer;
    }
    svg{
      width: 100%;
      height: 100%;
    }
  }

  &-form {
    &__wrapper {
      background-color: #fff;
      padding: 50px;
      box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06);
      border-radius: 6px;
    }

    &__input {
      position: relative;
      .text-input {
        height: 42px;
        font-size: 15px;
      }
    }

    &__input + &__input {
      margin-top: 20px;
    }

    &__footer {
      margin-top: 15px;
    }

    &__submit {
      height: 42px;
      width: 100%;
    }

    &__title {
      font-size: 21px;
      font-weight: 600;
      text-align: center;
    }

    &__content {
      margin-top: 20px;
    }
  }
}

.thanks-wrapper{
  min-height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  padding: 0 70px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06);
  border-radius: 6px;
  &__icon{
    width: 47px;
    height: 47px;
    background: #DAFFD3;
    border-radius: 18px;

    display: flex;
    align-items: center;
    justify-content: center;

    margin-bottom: 20px;
    svg{
      width: 25px;
      height: 18px;
    }
  }
  &__title,&__text{
    text-align: center;
  }
  &__title{
    font-size: 21px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  &__text{
    color:#151515;
    font-size: 15px;
    line-height: 20px;
  }
}

@media screen and (min-width: 991px) {
  .popup{
    &__close{
      display: none;
    }
  }
}

@media screen and (min-width: 450px) {
  .popup{
    &__close{
      top: 0;
      right: -40px;
    }
  }
}